/* eslint-disable @typescript-eslint/ban-ts-comment */
import { App } from 'vue'
import { Router } from 'vue-router'
import * as Sentry from '@sentry/vue'
import { BrowserTracing } from '@sentry/tracing'
import { ElMessageBox, ElNotification } from 'element-plus'
import 'element-plus/theme-chalk/el-notification.css'

import { eventToSentryRequest, sessionToSentryRequest } from '@sentry/core'
import { Event, Response, SentryRequest, Session, TransportOptions } from '@sentry/types'
import { SentryError, supportsReferrerPolicy, SyncPromise } from '@sentry/utils'
import { BaseTransport } from '@sentry/browser/esm/transports/base'
import { FetchImpl, getNativeFetchImplementation } from '@sentry/browser/esm/transports/utils'
import { apibase } from './apibase'

export async function init(app: App, router: Router) {
    document.head.appendChild(document.createElement('script')).src =
        'https://hm.baidu.com/hm.js?9fa0c980766e6a8646c0f814aa40b130'
    ;((window, website, root) => {
        const {
            screen: { width, height },
            navigator: { language },
            location,
            document,
            history,
        } = window
        const { hostname, pathname, search } = location
        const _data = 'data-'
        const endpoint = `${root}/ev`
        const screen = `${width}x${height}`
        const eventRegex = /data-x-ev-([\w-_]+)/
        const eventNameAttribute = _data + 'x-ev'
        const delayDuration = 300

        /* Helper functions */

        const hook = (_this: any, method: any, callback: any) => {
            const orig = _this[method]

            return (...args: any[]) => {
                callback(...args)
                return orig.apply(_this, args)
            }
        }

        const getPath = (url: string) => {
            if (url.substring(0, 4) === 'http') {
                return '/' + url.split('/').splice(3).join('/')
            }
            return url
        }

        const getPayload = () => ({
            website,
            hostname,
            screen,
            language,
            title: document.title,
            url: currentUrl,
            referrer: currentRef,
        })

        /* Tracking functions */

        const trackingDisabled = () => {
            // @ts-expect-error
            const { doNotTrack, navigator, external } = window

            const msTrackProtection = 'msTrackingProtectionEnabled'
            const msTracking = () => {
                // @ts-expect-error
                return external && msTrackProtection in external && external[msTrackProtection]()
            }

            // @ts-expect-error
            const dnt = doNotTrack || navigator.doNotTrack || navigator.msDoNotTrack || msTracking()

            return dnt == '1' || dnt === 'yes'
        }

        const handlePush = (_state: unknown, title: string, url: string) => {
            if (!url) return

            currentRef = currentUrl
            currentUrl = getPath(url.toString())

            if (currentUrl !== currentRef) {
                setTimeout(track, delayDuration)
            }
        }

        const handleClick = () => {
            const trackElement = (el: HTMLElement): any => {
                const attr = el.getAttribute.bind(el)
                const eventName = attr(eventNameAttribute)

                if (eventName) {
                    const eventData = {} as Record<string, any>

                    el.getAttributeNames().forEach((name: string) => {
                        const match = name.match(eventRegex)

                        if (match) {
                            eventData[match[1]] = attr(name)
                        }
                    })

                    return track(eventName, { data: eventData })
                }
                return Promise.resolve()
            }

            const callback = (e: any) => {
                const findATagParent = (rootElem: HTMLElement | null, maxSearchDepth: number) => {
                    let currentElement = rootElem
                    for (let i = 0; i < maxSearchDepth; i++) {
                        if (currentElement?.tagName === 'A') {
                            return currentElement
                        }
                        currentElement = currentElement?.parentElement || null
                        if (!currentElement) {
                            return null
                        }
                    }
                    return null
                }

                const el = e.target
                const anchor = el.tagName === 'A' ? el : findATagParent(el, 10)

                if (anchor) {
                    const { href, target } = anchor
                    const external =
                        target === '_blank' || e.ctrlKey || e.shiftKey || e.metaKey || (e.button && e.button === 1)
                    const eventName = anchor.getAttribute(eventNameAttribute)

                    if (eventName && href) {
                        if (!external) {
                            e.preventDefault()
                        }
                        return trackElement(anchor).then(() => {
                            if (!external) location.href = href
                        })
                    }
                } else {
                    trackElement(el)
                }
            }

            document.addEventListener('click', callback, true)
        }

        const send = (payload: any) => {
            if (trackingDisabled()) return
            const headers = {
                'Content-Type': 'application/json',
            } as Record<string, string>
            if (typeof cache !== 'undefined') {
                headers['x-ev-cache'] = cache
            }
            return fetch(endpoint, {
                method: 'POST',
                body: JSON.stringify({ type: 'event', payload }),
                headers,
            })
                .then((res) => res.text())
                .then((text) => {
                    cache = text
                })
        }

        const track = (obj?: any, data?: any) => {
            if (typeof obj === 'string') {
                return send({
                    ...getPayload(),
                    name: obj,
                    data: typeof data === 'object' ? data : undefined,
                })
            } else if (typeof obj === 'object') {
                return send(obj)
            } else if (typeof obj === 'function') {
                return send(obj(getPayload()))
            }
            return send(getPayload())
        }

        // @ts-expect-error
        if (!window.$ev) {
            // @ts-expect-error
            window.$ev = (obj, data) => track(obj, data)
        }

        let currentUrl = `${pathname}${search}`
        let currentRef = document.referrer
        let cache: string
        let initialized: boolean

        if (!trackingDisabled()) {
            history.pushState = hook(history, 'pushState', handlePush)
            history.replaceState = hook(history, 'replaceState', handlePush)
            handleClick()
            const init = () => {
                if (document.readyState === 'complete' && !initialized) {
                    track()
                    initialized = true
                }
            }
            document.addEventListener('readystatechange', init, true)
            init()
        }
    })(window, '8dd6307d-d7ff-4b0a-be63-4e6f9520334e', await apibase('', 'global'))

    Sentry.init({
        app,
        dsn: process.env.VUE_APP_SENTRY,
        integrations: [
            new BrowserTracing({
                routingInstrumentation: Sentry.vueRouterInstrumentation(router),
                tracingOrigins: ['77.cocogoat.work', '77.cocogoat.cn', /^\//],
            }),
        ],
        tracesSampleRate: 0.5,
        release: process.env.VUE_APP_GIT_SHA,
        environment: process.env.VUE_APP_SINGLEFILE === 'true' ? 'singlefile' : process.env.NODE_ENV,
        transport: SimpleFetchTransport,
    })
}
let currentEmail = ''
export async function setUser(email: string) {
    if (currentEmail) return
    currentEmail = email
    Sentry.setUser({
        email,
    })
}

export async function report() {
    let input
    try {
        input = await ElMessageBox.prompt('', '', {
            inputType: 'textarea',
            inputPlaceholder: ' xxx yyy ',
        })
    } catch (e) {
        return
    }
    if (!input.value) return
    const comments = input.value
    const evid = Sentry.captureMessage('\n' + comments)
    const u = new URL(process.env.VUE_APP_SENTRY || '')
    u.username = ''
    u.password = ''
    u.pathname = u.pathname.split('/').slice(0, -1).join('/') + '/api/embed/error-page/'
    u.searchParams.set('eventId', evid)
    console.log(comments)

    const reportData = {
        name: 'anonymous-report',
        email: currentEmail || `anonymous-report@cocogoat.work`,
        comments,
    }
    try {
        const res = await fetch(u.toString(), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            body: new URLSearchParams(reportData).toString(),
        })
        if (res.ok) {
            ElNotification({
                title: '',
                message: 'ID: ' + evid + '  ID ',
                type: 'success',
                customClass: 'feedback-notification',
                duration: 0,
            })
        } else {
            throw new Error('Got a non-200 response')
        }
    } catch (e) {
        ElNotification({
            title: '',
            message: (e as Error).message,
            type: 'error',
        })
    }
}

/* Sentry Transport */
export class SimpleFetchTransport extends BaseTransport {
    /**
     * Fetch API reference which always points to native browser implementation.
     */
    private _fetch: typeof fetch

    public constructor(options: TransportOptions, fetchImpl: FetchImpl = getNativeFetchImplementation()) {
        super(options)
        this._fetch = fetchImpl
    }

    /**
     * @inheritDoc
     */
    public sendEvent(event: Event): PromiseLike<Response> {
        return this._sendRequest(eventToSentryRequest(event, this._api), event)
    }

    /**
     * @inheritDoc
     */
    public sendSession(session: Session): PromiseLike<Response> {
        return this._sendRequest(sessionToSentryRequest(session, this._api), session)
    }

    /**
     * @param sentryRequest Prepared SentryRequest to be delivered
     * @param originalPayload Original payload used to create SentryRequest
     */
    _sendRequest(sentryRequest: SentryRequest, originalPayload: Event | Session): PromiseLike<Response> {
        if (this._isRateLimited(sentryRequest.type)) {
            this.recordLostEvent('ratelimit_backoff', sentryRequest.type)
            const err = {
                name: 'SentryError',
                message: 'too many requests',
                event: originalPayload,
                type: sentryRequest.type,
                reason: `Transport for ${sentryRequest.type} requests locked till ${this._disabledUntil(
                    sentryRequest.type,
                )} due to too many requests.`,
                status: 429,
            } as Error
            return Promise.reject(err)
        }

        const options: RequestInit = {
            body: sentryRequest.body,
            method: 'POST',
            // Despite all stars in the sky saying that Edge supports old draft syntax, aka 'never', 'always', 'origin' and 'default
            // https://caniuse.com/#feat=referrer-policy
            // It doesn't. And it throw exception instead of ignoring this parameter...
            // REF: https://github.com/getsentry/raven-js/issues/1233
            referrerPolicy: (supportsReferrerPolicy() ? 'origin' : '') as ReferrerPolicy,
        }
        if (this.options.fetchParameters !== undefined) {
            Object.assign(options, this.options.fetchParameters)
        }
        if (this.options.headers !== undefined) {
            options.headers = this.options.headers
        }

        return this._buffer
            .add(
                () =>
                    new SyncPromise<Response>((resolve, reject) => {
                        this._fetch(sentryRequest.url.replace(/sentry_(.*?)=/g, 'f9f3bed2daf73dc6_$1='), options)
                            .then((response) => {
                                const headers = {
                                    'x-sentry-rate-limits': response.headers.get('X-Sentry-Rate-Limits'),
                                    'retry-after': response.headers.get('Retry-After'),
                                }
                                this._handleResponse({
                                    requestType: sentryRequest.type,
                                    response,
                                    headers,
                                    resolve,
                                    reject,
                                })
                            })
                            .catch(reject)
                    }),
            )
            .then(undefined, (reason) => {
                // It's either buffer rejection or any other xhr/fetch error, which are treated as NetworkError.
                if (reason instanceof SentryError) {
                    this.recordLostEvent('queue_overflow', sentryRequest.type)
                } else {
                    this.recordLostEvent('network_error', sentryRequest.type)
                }
                throw reason
            })
    }
}
